/* Gallery Slideshow Container */
.gallery-slideshow-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  position: relative;
  max-width: 100%;
  overflow: visible;
}

/* Gallery Slide Images */
.gallery-slide {
  width: 280px;
  height: 180px;
  border-radius: 12px;
  object-fit: cover;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  transition: transform 0.4s ease, box-shadow 0.4s ease;
  user-select: none;
  flex-shrink: 0;
}

/* Enlarged Image in Carousel */
.gallery-slide.enlarged-image {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 80vw;
  max-width: 80vw;
  max-height: 80vh;
  height: auto;
  transform: translate(-50%, -50%);
  z-index: 1100;
  box-shadow: 0 12px 30px rgba(0,0,0,0.6);
  cursor: zoom-out;
  border-radius: 14px;
  transition: transform 0.3s ease;
}

/* Overlay Background */
.gallery-overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0,0,0,0.85);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: default;
}

/* Overlay Content Container */
.gallery-overlay-content {
  position: relative;
  display: flex;
  align-items: center;
  gap: 20px;
  max-width: 95vw;
  max-height: 90vh;
}

/* Close Button */
.gallery-overlay-close {
  position: fixed;
  top: 20px;
  right: 20px;
  background: rgba(255,255,255,0.9);
  border: none;
  font-size: 32px;
  font-weight: bold;
  color: #222;
  cursor: pointer;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  line-height: 32px;
  text-align: center;
  transition: background 0.3s ease;
  z-index: 1200;
}

.gallery-overlay-close:hover {
  background: #222;
  color: #fff;
}

/* Navigation Buttons */
.gallery-overlay-prev,
.gallery-overlay-next {
  background: rgba(255,255,255,0.9);
  border: none;
  font-size: 36px;
  font-weight: bold;
  color: #222;
  cursor: pointer;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  line-height: 36px;
  text-align: center;
  transition: background 0.3s ease;
  user-select: none;
  z-index: 1200;
}

.gallery-overlay-prev:hover,
.gallery-overlay-next:hover {
  background: #222;
  color: #fff;
}
